<template>
  <div>
    <Mheader>首页</Mheader>
    <div class="content">
      <Swiper :swiperSlides="sliders"></Swiper>
      <div class="container">
        <h3>热门游戏显卡</h3>
        <ul>
          <li v-for="(GPU,index) in GPUs" :key="index">
            <img :src="GPU.GPUImg">
            <span>{{GPU.GPUName}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Mheader from '../base/Mheader.vue';
  import Swiper from '../base/Swiper.vue';
  import {getSliders, getHotGPUs} from '../api';

  export default {
    name: "home",
    components: {
      Mheader, Swiper
    },
    data() {
      return {
        sliders: [],
        GPUs: []
      }
    },
    created() {
      this.getSlidersData();
      this.getGPUsData();
    },
    methods: {
      async getSlidersData() {
        let {data: sliders} = await getSliders();
        this.sliders = sliders;
      },
      async getGPUsData() {
        let {data: GPUs} = await getHotGPUs();
        this.GPUs = GPUs;
      }
    }
  }
</script>

<style scoped lang="less">
  .container {
    width: 95%;
    margin: 0 auto;

    ul {
      padding: 0;
      margin: 0;
      display: flex;
      flex-wrap: wrap; //默认不换行，设置wrap换行
      li {
        width: 50%;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
        padding: 8px;
        margin: 5px 0;
        box-sizing: border-box;

        img {
          width: 100%;
        }
      }
    }

  }
</style>



